.project-form__project-name {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 0 auto;
  padding: 24px 32px 48px 32px;
}

.project-form__project-name > h3 {
  margin-top: 0;
}

.project-form__footer {
  flex: none;
  height: 64px;
  padding: 14px 32px;
  background-color: var(--ktor-primary-cold-background-color);
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  overflow: hidden;
  align-items: center;
}

.project-form__footer-link-block {
  max-width: calc(100% - 120px);
}

.project-form__footer > *:nth-child {
  margin-left: 8px;
}
