.features-list__container {
  display: flex;
}

.features-list__search-panel {
  padding: 12px 24px;
}

.features-list__list {
  padding: 0 24px 24px 24px;
  flex: 1 1 auto;
  overflow-y: auto;
}

.features-list__no-content-container {
  margin: 0 auto;
  padding-top: 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.features-list__loading {
  padding-left: 8px;
}
