.added-features {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  bottom: 64px;
  overflow: auto;
  background-color: white;
  transition: top 0.3s;
  z-index: 1;
}

.added-features_shown {
  top: 0;
  bottom: 64px;
  z-index: 2;
}

.added-features__container {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow-y: hidden;
}

.added-features__search {
  margin-top: 12px;
  padding-right: 24px;
  padding-left: 24px;
}

.added-features__header {
  box-sizing: border-box;
  padding: 4px 24px;
  min-height: 64px;
  flex: 0 1 64px;
  background: #f0673e;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.added-features__header-text {
  color: white;
}

.added-features__header-icon {
  color: white;
  line-height: 20px;
  cursor: pointer;
}

.added-features__no-content {
  /*position: absolute;*/
  /*top: 0;*/
  /*left: 0;*/
  /*right: 0;*/
  /*bottom: 20%;*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
