.features-list-panel__description {
  padding: 24px 24px 0 24px;
}
.features-list-panel__description h3 {
  margin: 0;
}

.features-list-panel__footer {
  flex: none;
  box-sizing: border-box;
  padding: 4px 24px;
  width: 100%;
  height: 64px;
  background: #f0673e;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.features-list-panel__form-toggle-panel {
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: stretch;
  max-width: 40%;
}

.features-list-panel__form-toggle-panel-margin {
  width: 100%;
}

.features-list-panel__footer-label {
  color: white;
  white-space: nowrap;
}

.features-list-panel__clear {
  margin-left: 3px;
  color: white;
  opacity: 0.8;
  white-space: nowrap;
  cursor: pointer;
}

.features-list-panel__clear > svg {
  vertical-align: middle;
}

.features-list-panel__clear:hover {
  opacity: 1;
}
