.feature-info {
  flex: 1 1 auto;
  overflow: auto;
  padding: 16px 32px;
}

@media screen and (max-width: 640px) {
  .feature-info {
    display: none;
  }
}

.feature-info__header {
  display: flex;
  align-items: center;
  padding-bottom: 1em;
  gap: 1em;
  box-shadow: 0 0.35em rgba(0,0,0,0.1);
  margin-bottom: 2em;
}

.feature-info__header-info {
  flex: 1 1 auto;
}

.feature-info__header-info > h3 {
  margin: 0;
}

.feature-info__vendor-details {
  display: flex;
  justify-content: flex-start;
  gap: 0.4em;
}


.feature-info-markdown ul,
.feature-info-markdown li,
.feature-info-markdown p {
  font-size: 15px;
}

.feature-info-markdown ul {
  padding-left: 19px;
}

.feature-info-markdown h2,
.feature-info-markdown h3,
.feature-info-markdown h4 {
  margin-bottom: 5px;
  margin-top: 20px;
  color: #27282c;
}
