
.selected-plugins {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  align-self: flex-end;
}

.selected-plugins__plugin {
  background-color: var(--ktor-primary-cold-background-color);
  color: var(--rs-color-white);
  line-height: 1;
  padding: 6px 6px 6px 10px;
  display: flex;
  align-items: center;
  gap: 4px;
  border-radius: 4px;
  cursor: pointer;
}
.selected-plugins__plugin .close-icon {
  padding: 2px 2px 0 2px;
  border-radius: 2px;
}
.selected-plugins__plugin .close-icon:hover {
  background-color: var(--ktor-primary-cold-background-color-dim);
}
.selected-plugins__remove-list {
  margin-top: 4px;
  padding-left: 16px;
}