.generate-panel {
  display: grid;
  grid-template-columns: minmax(200px, 1fr) auto;
  justify-content: space-between;
  padding: 24px 0;
  gap: 24px 48px;
  /*box-shadow: 6px 6px 0 var(--ktor-primary-cold-background-color-faded);*/
  /*background-color: var(--ktor-primary-cold-background-color);*/
  /*background: linear-gradient(300deg, rgba(63,94,251,1) 0%, rgba(252,70,107,1) 100%);;*/
  /*background: linear-gradient(170deg, rgba(242,103,53,1) 0%, rgba(224,105,79,1) 11%, rgba(49,65,212,1) 60%, rgba(113,124,221,1) 100%);*/
}
@media only screen and (max-width: 1100px) {
  .generate-panel h1 {
    font-size: 24px;
  }
}

@media only screen and (max-width: 975px) {
  .generate-panel {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .generate-panel__blurb {
    margin-bottom: 24px;
  }
}

@media only screen and (max-width: 700px) {
  .generate-panel__form {
    flex-direction: column;
  }
  .generate-panel__separator {
    display: none;
  }
}

.generate-panel__blurb {}
.generate-panel__blurb h1 {
  margin-top: 0;
}

.generate-panel__separator {
  border-left: 1px solid var(--ktor-borders-color);
  align-self: stretch;
}

.generate-panel__form {
  display: flex;
  padding: 36px 48px 48px 48px;
  gap: 36px;
  align-items: center;
  border: 1px solid var(--ktor-borders-color);
  background: linear-gradient(170deg, rgba(128, 128, 128, 0.15), rgba(128, 128, 128, 0.06) 80%);
  border-radius: 12px;
}

.generate-panel__details {
  display: grid;
  gap: 4px 0;
  grid-template-columns: 24px auto;
  grid-template-rows: 22px 22px 22px;
  line-height: 1;
  align-items: center;
  justify-items: start;
}
.generate-panel__details svg {
  width: 16px;
  height: 16px;
}
.generate-panel__details .plugins {
  grid-column: 1 / span 2;
  display: flex;
  align-items: center;
  gap: 4px;
}
.generate-panel__details .plugins > div {
  font-size: small;
  background-color: var(--ktor-background-dark);
  color: var(--ktor-background-color);
  border-radius: 4px;
  padding: 3px 6px;
}

.generate-panel__details span {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.generate-panel__download {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

