.project-settings {
  box-sizing: border-box;
  width: 100%;
  padding: 0 32px;
  max-height: 0;
  overflow: hidden;
  transition:
    max-height 0.3s,
    padding 0.3s;
  background-color: var(--ktor-primary-cold-background-color);
  --rs-color-primary-light-theme: var(--ktor-primary-warm-background-color);
  --rs-color-primary-dark-theme: var(--ktor-primary-warm-background-color);
  --rs-color-primary-light-theme-80: var(--ktor-secondary-warm-background-color);
  --rs-color-primary-dark-theme-80: var(--ktor-secondary-warm-background-color);
}

.settings-preamble {
  grid-area: preamble;
  padding: 16px 24px 0 24px;
  max-width: 540px;
}

.project-settings_expanded {
  padding: 4px 32px 32px 32px;
  max-height: 1000px;
}

.project-settings__row {
  margin-bottom: 12px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.project-settings__row:first-child {
  padding-top: 8px;
}

.project-settings__row:first-child > .project-settings__row-label {
  padding-top: 0;
}

.project-settings__row:last-child {
  margin-bottom: 12px;
}

.project-settings__row-label {
  width: 150px;
  padding-right: 16px;
  padding-top: 5px;
}

.project-settings__row-input {
  flex: 1 0 150px;
  --rs-color-black: rgba(255, 255, 255, 0.1);
  --rs-color-grey-90: rgba(255, 255, 255, 0.1);
}

/*
 * Sadly, this is the only way to override the dark theme background color
 * for inputs and selects for RescUI.
 */
.project-settings__row-input div[class^="_wrapper_"],
.project-settings__row-input div[class^="_main_"] {
  background-color: transparent !important;
}

.project-settings__row-input > div:first-child {
  background: rgba(255, 255, 255, 0.05);
}

