#kt-wiz {
  --ktor-primary-warm-background-color: #f26735;
  --ktor-primary-cold-background-color: #6473ff;

  --ktor-secondary-warm-background-color: #ff9159;
  --ktor-secondary-cold-background-color: #719dff;

  --ktor-pale-warm-background-color: #fef0eb;
  --ktor-pale-cold-background-color: #e0e3fe;

  --ktor-max-island-height: max(calc(100vh - 208px), 208px);

  --ktor-island-box-shadow: 8px 8px 0 0 rgba(0, 0, 0, 0.05);
  --ktor-borders-color: #d4d4d5;

  --rs-color-primary-light-theme-80: var(--ktor-secondary-warm-background-color);
}

@media screen and (max-width: 640px) {
  #kt-wiz {
    --ktor-header-height: 110px;
    --ktor-max-island-height: max(calc(100vh - 248px), 248px);
  }
}

div[data-test="ring-popup"] {
  box-shadow: var(--ktor-island-box-shadow);
  border-radius: 0;
  border-color: var(--ktor-borders-color);
}

#kt-wiz {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

#kt-wiz .ktor-generator-left-panel {
  position: relative;
  border: 1px solid var(--ktor-borders-color);
  box-shadow: var(--ktor-island-box-shadow);
  margin: 12px 24px;
  display: flex;
  flex-direction: column;
}

#kt-wiz > main {
  display: grid;
  overflow: auto;
  grid-template-areas: "preamble preamble" "form details";
  grid-template-columns: minmax(320px, 500px) auto;
}

#kt-wiz > main.plugins {
  height: calc(100vh - 85px);
  grid-auto-rows: minmax(50px, max-content);
}
